/* Bootstrap Grid Columns Settings */
$grid-columns:         12 !global;
$grid-gutter-width:     30px !global;
$grid-row-columns:      6 !global;
$container-max-widths:(
        xs: 500px,
        sm: 540px,
        md: 720px,
        lg: 960px,
        xl: 1180px,
        hd: 1280px,
) !global;
$grid-breakpoints:(
        xs: 0,
        sm: 576px,
        md: 768px,
        lg: 992px,
        xl: 1400px,
        hd: 1600px
) !global;
/* Bootstrap Grid Columns Settings */

/* Button & Input Settings */
$btn-font-weight:               500 !global;
$border-radius:                 .25rem !global;
$border-radius-lg:              .3rem !global;
$border-radius-sm:              .2rem !global;
//$btn-padding-y:                .7rem !global;
//$btn-padding-x:               1.5rem !global;
$btn-padding-y-lg:              .7rem !global;
$btn-padding-x-lg:              1.5rem !global;
/* Button & Input Settings */

/* Color Settings */
$white:    $global-clr-white !global;
$gray-100: $global-clr-light-gray !global;
$gray-800: $global-clr-soft-gray !global;

$blue:    $global-clr-soft-blue !global;
$red:     $global-clr-red !global;
$orange:  $global-clr-orange !global;
$yellow:  $global-clr-orange !global;
$green:   $global-clr-green !global;
/* Color Settings */